<template>
<v-col>
    <v-row>
      <v-input>Table</v-input>
    </v-row>
    <v-row>
      <v-text-field v-model="tableNm" label="Table Name" outlined readonly></v-text-field>
    </v-row>
    <v-row>
      <v-text-field v-model="shortDesc" label="Short Description" outlined readonly></v-text-field>
    </v-row>
    <v-row>
      <v-data-table
        :headers="headers"
        :items="colInfo"
      >
        <template v-slot:item.k="{ item }">
        <v-simple-checkbox v-model="item.k" v-if="item.k ==='Y'" disabled></v-simple-checkbox>
        </template>
      </v-data-table>
    </v-row>  
  </v-col>
</template>
<script>
import { EventBus } from "../../event-bus";
export default {
  data() {
    return {
      tableNm: "",
      shortDesc: "",
      colInfo: [],
      headers: [
        { text: "Field", value: "field" },
        { text: "K", value: "k" },
        { text: "Type", value: "type" },
        { text: "Length", value: "length" },
        { text: "Nullable", value: "nullable" },
        { text: "DF", value: "df" },
        { text: "Description", value: "desc" },
      ],
    };
  },
  created() {
    EventBus.$emit('title', "Field Information");
    this.$http.get("/fieldinfo/"+this.$route.query.tableNm).then(response => {
      this.tableNm = response.data[0].TABLE_NM;
      this.shortDesc = response.data[0].SHORT_DESC;
      this.colInfo = response.data[0].COL_INFO;
    });
  }
};
</script>